export default function Hotel() {
  const [floor, setFloor] = useState(floors.length);

  const [firstInit, setFirstInit] = useState(false)

  const [isOpenArticleModal, setIsOpenArticleModal] = useState(false)
  const [isOpenVideoModal, setIsOpenVideoModal] = useState(false)
  const [isOpenCarouselModal, setIsOpenCarouselModal] = useState(false)
  const [isOpenMovieModal, setIsOpenMovieModal] = useState(false)
  const [isOpenAtriumModal, setIsOpenAtriumModal] = useState(false)

  const [activePerson, setActivePerson] = useState(null)

  const openArticleModal = () => {
    setIsOpenArticleModal(true)
  }

  const closeArticleModal = () => {
    setIsOpenArticleModal(false)
  }

  const openVideoModal = () => {
    setIsOpenVideoModal(true)
  }

  const closeVideoModal = () => {
    setIsOpenVideoModal(false)
  }

  const openCarouselModal = () => {
    setIsOpenCarouselModal(true)
  }

  const closeCarouselModal = () => {
    setIsOpenCarouselModal(false)
  }

  const openMovieModal = () => {
    setIsOpenMovieModal(true)
  }

  const closeMovieModal = () => {
    setIsOpenMovieModal(false)
  }

  const openAtriumModal = () => {
    setIsOpenAtriumModal(true)
  }

  const closeAtriumModal = () => {
    setIsOpenAtriumModal(false)
  }

  const deactivateMenuItems = (menuChunk) => {
    menuChunk.forEach(chunk => {

      chunk.forEach(item => {
        item.active = false
      })

    })
  }

  const activateMenuItem = (menuItem, menuChunk) => {
    deactivateMenuItems(menuChunk)

    menuChunk.forEach(chunk => {
      chunk.forEach(item => {
        if (menuItem.type === item.type) {
          item.active = true
        }
      })
    })
  }

  const closeAllModals = useCallback(() => {
    setIsOpenArticleModal(false)
    setIsOpenVideoModal(false)
    setIsOpenCarouselModal(false)
    setIsOpenMovieModal(false)
    setIsOpenAtriumModal(false)
  }, [])

  //  dom-   
  let scrollContainer = React.createRef();

  let detailVideo = React.createRef();

  /**
   *   
   * @param floor
   * @returns {*}
   */
  let getPrevFloor = (floor) => {
    return floor > 1 ? floor - 1 : null
  }

  /**
   *   
   * @param floor
   * @param length
   * @returns {*}
   */
  let getNextFloor = (floor, length) => {
    return floor > length - 1 ? null : floor + 1
  }

  let getFloorByIndex = (index, floors) => {
    let result = null

    floors.forEach(floor => {
      if (floor.value === index) {
        result = floor
      }
    })

    return result
  }

  /**
   * /
   * @param num
   * @returns {number}
   */
  let isOdd = (num) => {
    return num % 2
  }

  let isFirst = (num) => {
    return num === 0
  }

  let isLast = (num, collection) => {
    return num === collection.length - 1
  }

  let chunkArray = (myArray, chunkSize) => {
    let index = 0
    let arrayLength = myArray.length
    let tempArray = []
    let myChunk

    for (index = 0; index < arrayLength; index += chunkSize) {
      myChunk = myArray.slice(index, index + chunkSize);
      tempArray.push(myChunk);
    }

    return tempArray;
  }

  let prepareFloors = (floors) => {
    return floors.map((floor, index) => {
      floor.isFirst = isFirst(index)
      floor.isLast = isLast(index, floors)
      floor.isOdd = isOdd(index)
      return floor
    })
  }

  const onClickPersonInfoHandler = (person, e) => {
    closeAllModals()
    setActivePerson(person)
    openArticleModal()
  }

  const onClickVideoPreviewHandler = (person, e) => {
    closeAllModals()
    setActivePerson(person)
    openVideoModal()
  }

  const onClickCarouselPreviewHandler = (menuItem, menuChunk) => {
    closeAllModals()
    activateMenuItem(menuItem, menuChunk)
    openCarouselModal()
  }

  const onClickMovieHandler = (menuItem, menuChunk) => {
    closeAllModals()
    activateMenuItem(menuItem, menuChunk)
    openMovieModal()
  }

  const onClickAtriumHandler = (menuItem, menuChunk) => {
    closeAllModals()
    activateMenuItem(menuItem, menuChunk)
    openAtriumModal()
  }

  let getSections = (floors) => {
    let sectionsChunk = chunkArray(prepareFloors(floors), 2)

    return sectionsChunk.map((section, sectionIndex) => {
      return <section className={`${styles.section}`} key={sectionIndex}>
        <div className={styles.sectionInner}>
          {
            section.map((floorItem, floorIndex) => {
              return <div className={`panel ${floorItem.isFirst ? 'panel-first' : ''} ${floorItem.isLast ? 'panel-last' : ''} ${floorItem.value === floor ? styles.panelShow : ''}`} key={floorItem.value}>
                <div className={styles.panelCenter}/>
                <div className={`${styles.window} ${floorItem.value === floor ? styles.windowShow : ''}`}>
                  <div
                    className={`${styles.windowPreviewVideo} ${floorItem.value === floor ? styles.windowPreviewVideoShow : ''}`}
                    onClick={((floorItem.value === floor) || floorItem.value === 0) ? onClickVideoPreviewHandler.bind(this, floorItem) : null}
                  >
                    <video autoPlay playsInline loop muted src={floorItem.person.previewVideo}/>
                    <div className={styles.playIcon}>
                      <Play />
                    </div>
                  </div>
                </div>
                <div className={`${styles.person} ${floorItem.value === floor ? styles.personShow : ''}`} onClick={((floorItem.value === floor) || floorItem.value === 0) ? onClickPersonInfoHandler.bind(this, floorItem) : null}>
                  <div className={styles.personTitle}>
                    <div className={styles.personName}>{floorItem.person.name} {floorItem.person.patronymic}</div>
                    <div className={`${styles.personSurname} ${floorItem.person.surname.length > 10 ? styles.personSurnameMiddle : ''}`}>{floorItem.person.surname}</div>
                  </div>
                  <div className={styles.personPosition} dangerouslySetInnerHTML={{
                    __html: floorItem.person.position
                  }}/>
                  { floorItem.person.article.title && <div className={styles.articleTitle}>
                      <div className={styles.lineBox}>
                        <div className={styles.lineBoxDot}>
                          <Rhombus/>
                        </div>
                      </div>
                      <div className={styles.articleTitleText}>
                        {floorItem.person.article.title}
                      </div>
                    </div>
                  }
                </div>
              </div>
            })
          }
          <div className={styles.sectionParapet}/>
        </div>
      </section>
    })
  }

  let getMenuItems = () => {
    let menuChunk = chunkArray(menu, 2)

    return <div className={styles.menu}>
      {
        menuChunk.map((menuChunkItem, index) => {
          return <div className={styles.menuSection} key={index}>
            {
              menuChunkItem.map((menuItem, chunkIndex) => {
                return <div className={`${styles.menuItemTitle} ${menuItem.active ? styles.menuItemTitleActive : ''}`} key={menuItem.title} onClick={(e) => {
                  const handlers = {
                    lib: onClickCarouselPreviewHandler.bind(this, menuItem, menuChunk),
                    movie: onClickMovieHandler.bind(this, menuItem, menuChunk),
                    atrium: onClickAtriumHandler.bind(this, menuItem, menuChunk),
                  }

                  if (handlers[menuItem.type]) {
                    handlers[menuItem.type](e)
                  }
                }}>
                  {menuItem.title}
                  {
                    chunkIndex === 0 && <span className={styles.menuItemRhombus}><Rhombus/></span>
                  }
                </div>
              })
            }
          </div>
        })
      }
    </div>
  }

  //      ScrollMagicfloors
  useEffect(() => {
    if (!firstInit) {
      let controller = new ScrollMagic.Controller()
      let $allSections = scrollContainer.current.querySelectorAll('.panel')


      floors.forEach((floor, index) => {
        let tl = new TimelineMax();
        let $section = $allSections[index]

        tl.from($allSections[index], 0.25, {
          ease: Back.easeOut,
          onStart() {
            setFloor(floor.value)
          },
          onReverseComplete() {
            setFloor(floor.value < floors.length ? floor.value + 1 : floors.length)
          }
        });

        floor.scene = new ScrollMagic.Scene({
          triggerElement: $section,
          triggerHook: 0.5
        })
          .setTween(tl)
          /*.addIndicators()*/
          .addTo(controller);
      })

      setFirstInit(true)
    }
  }, [firstInit, scrollContainer]);

  useEffect(() => {
    if (detailVideo.current) {
      detailVideo.current.addEventListener('ended', () => {
        closeAllModals()
      })
    }
  }, [detailVideo, closeAllModals])

  //  
  useEffect(() => {
    window.scrollTo({
      top: document.body.scrollHeight,
      behavior: "smooth"
    });

  }, [])

  useEffect(() => {
    if (isOpenArticleModal || isOpenVideoModal || isOpenCarouselModal || isOpenMovieModal || isOpenAtriumModal) {
      window.document.body.classList.add('overflowed')

      if (isOpenVideoModal) {
        window.document.body.classList.add('videoActive')
        window.document.body.classList.remove('articleActive')
        window.document.body.classList.remove('libActive')
        window.document.body.classList.remove('movieActive')
        window.document.body.classList.remove('atriumActive')
      }

      if (isOpenArticleModal) {
        window.document.body.classList.add('articleActive')
        window.document.body.classList.remove('videoActive')
        window.document.body.classList.remove('libActive')
        window.document.body.classList.remove('movieActive')
        window.document.body.classList.remove('atriumActive')
      }

      if (isOpenCarouselModal) {
        window.document.body.classList.add('libActive')
        window.document.body.classList.remove('articleActive')
        window.document.body.classList.remove('videoActive')
        window.document.body.classList.remove('movieActive')
        window.document.body.classList.remove('atriumActive')
      }

      if (isOpenMovieModal) {
        window.document.body.classList.add('movieActive')
        window.document.body.classList.remove('articleActive')
        window.document.body.classList.remove('videoActive')
        window.document.body.classList.remove('libActive')
        window.document.body.classList.remove('atriumActive')
      }

      if (isOpenAtriumModal) {
        window.document.body.classList.add('atriumActive')
        window.document.body.classList.remove('articleActive')
        window.document.body.classList.remove('videoActive')
        window.document.body.classList.remove('libActive')
        window.document.body.classList.remove('movieActive')
      }
    } else {
      window.document.body.classList.remove('overflowed')
      window.document.body.classList.remove('videoActive')
      window.document.body.classList.remove('articleActive')
      window.document.body.classList.remove('libActive')
      window.document.body.classList.remove('movieActive')
      window.document.body.classList.remove('atriumActive')
    }
  }, [isOpenArticleModal, isOpenVideoModal, isOpenCarouselModal, isOpenMovieModal, isOpenAtriumModal])

  useEffect(() => {
    window.addEventListener('resize', closeAllModals);

    return () => {
      window.removeEventListener('resize', closeAllModals);
    };
  }, [closeAllModals]);

  return (
    <div className={styles.root}>
      <div className={styles.scrollContainer} ref={scrollContainer}>

        {
          getSections(floors)
        }

        <Modal
          className={`${styles.modal} ${styles.articleModal}`}
          width={'100%'}
          visible={isOpenArticleModal}
          onCancel={closeArticleModal}
          style={{ top: 0, bottom: 0, height: '100vh' }}
          bodyStyle={{padding: 0}}
          footer={false}
        >
          <div className={`${styles.modalArticleInner} ${activePerson?.isOdd ? styles.modalArticleInnerOdd  : styles.modalArticleInnerEven }`}>
            <div className={styles.detailArticleWrapper} onClick={closeArticleModal}>
              <PersonInfo {...activePerson} />
            </div>
          </div>

          <button className={styles.closeModal} onClick={closeArticleModal}>
            <div className={styles.closeModalIcon}>
              <Close/>
            </div>
          </button>

        </Modal>

        <Modal
          className={`${styles.modal} ${styles.videoModal}`}
          width={'100%'}
          style={{position: 'fixed', top: '0', bottom: '0', right: 0, left: 0, paddingBottom: 0}}
          bodyStyle={{height: '100vh', overflow: 'hidden', padding: 0}}
          visible={isOpenVideoModal}
          onCancel={closeVideoModal}
          footer={false}
        >
          <div className={`${styles.modalInner} ${activePerson?.isOdd ? styles.modalInnerOdd  : styles.modalInnerEven }`}>
            <div className={styles.detailVideoWrapper}>
              {
                isOpenVideoModal && activePerson.person.detailVideo &&
                <video className={styles.detailVideo} autoPlay playsInline src={activePerson.person.detailVideo} ref={detailVideo}/>
              }
            </div>
          </div>

          <button className={styles.closeModal} onClick={closeVideoModal}>
            <div className={styles.closeModalIcon}>
              <Close/>
            </div>
          </button>

        </Modal>

        <Modal
          className={`${styles.modal} ${styles.videoModal}`}
          width={'100%'}
          style={{position: 'fixed', top: '0', bottom: '0', right: 0, left: 0, paddingBottom: 0}}
          bodyStyle={{height: '100vh', overflow: 'hidden', padding: 0}}
          visible={isOpenCarouselModal}
          onCancel={closeCarouselModal}
          footer={false}
        >
          <div className={styles.carouselWrapper}>
            <div className={styles.carouselWrapperInner}>
              <Carousel/>
            </div>
          </div>

          <button className={`${styles.closeModal} ${styles.closeModalTopRight}`} onClick={closeCarouselModal}>
            <div className={styles.closeModalIcon}>
              <Close/>
            </div>
          </button>
        </Modal>

        <Modal
          className={`${styles.modal} ${styles.videoModal}`}
          width={'100%'}
          style={{position: 'fixed', top: '0', bottom: '0', right: 0, left: 0, paddingBottom: 0}}
          bodyStyle={{height: '100vh', overflow: 'hidden', padding: 0}}
          visible={isOpenMovieModal}
          onCancel={closeMovieModal}
          footer={false}
        >
          <div className={styles.carouselWrapper}>
            <div className={styles.carouselWrapperInner}>
              <Movie showMovie={isOpenMovieModal} src={movieSrc}/>
            </div>
          </div>

          <button className={`${styles.closeModal} ${styles.closeModalTopRight}`} onClick={closeMovieModal}>
            <div className={styles.closeModalIcon}>
              <Close/>
            </div>
          </button>
        </Modal>

        <Modal
          className={`${styles.modal} ${styles.videoModal}`}
          width={'100%'}
          style={{position: 'fixed', top: '0', bottom: '0', right: 0, left: 0, paddingBottom: 0}}
          bodyStyle={{height: '100vh', overflow: 'hidden', padding: 0}}
          visible={isOpenAtriumModal}
          onCancel={closeAtriumModal}
          footer={false}
        >
          <div className={styles.carouselWrapper}>
            <div className={styles.carouselWrapperInner}>
              <Movie showMovie={isOpenAtriumModal} src={atriumSrc}/>
            </div>
          </div>

          <button className={`${styles.closeModal} ${styles.closeModalTopRight}`} onClick={closeAtriumModal}>
            <div className={styles.closeModalIcon}>
              <Close/>
            </div>
          </button>
        </Modal>
      </div>

      <div className={styles.frame}>
        <div className={styles.floor}>
          <div className={styles.floorRound}>
            <FloorRoundSVG />
          </div>
          {
            getNextFloor(floor, floors.length) && <div className={`${styles.floorValue} ${styles.floorPrev} ${getNextFloor(floor, floors.length) > 9 ? styles.floorPrevMiddle : ''}`} onClick={() => {
              let toFloor = getFloorByIndex(getNextFloor(floor, floors.length), floors)
              if (toFloor) {
                window.scrollTo({
                  top: toFloor.scene.scrollOffset() + toFloor.scene.triggerElement().offsetHeight / 2,
                  behavior: "smooth"
                });
              }

            }}>{getNextFloor(floor, floors.length)}</div>
          }
          <div className={`${styles.floorValue} ${styles.floorCurrent} ${floor >  9 ? styles.floorCurrentMiddle : ''}`}>{floor}</div>
          {
            getPrevFloor(floor) && <div className={`${styles.floorValue} ${styles.floorNext} ${getPrevFloor(floor) > 9 ? styles.floorNextMiddle : ''}`} onClick={() => {
              let toFloor = getFloorByIndex(getPrevFloor(floor), floors)
              if (toFloor) {
                window.scrollTo({
                  top: toFloor.scene.scrollOffset() + toFloor.scene.triggerElement().offsetHeight / 2,
                  behavior: "smooth"
                });
              }
            }}>{getPrevFloor(floor)}</div>
          }
        </div>
      </div>

      {/* Begin borderLayer */}
      <div className={styles.borderLayer}>
        <i className={`${styles.borderLayerLine} ${styles.lineTop} ${styles.lineTopLeft}`}></i>
        <i className={`${styles.borderLayerLine} ${styles.lineTop} ${styles.lineTopRight}`}></i>
        <i className={`${styles.borderLayerLine} ${styles.lineBottom}`}></i>
        <i className={`${styles.borderLayerLine} ${styles.lineLeft}`}></i>
        <i className={`${styles.borderLayerLine} ${styles.lineRight}`}></i>
        <b className={`${styles.corner} ${styles.cornerTop} ${styles.cornerLeft}`}>
          <CornerOpacity/>
        </b>
        <b className={`${styles.corner} ${styles.cornerTop} ${styles.cornerRight}`}>
          <CornerOpacity/>
        </b>
        <b className={`${styles.corner} ${styles.cornerBottom} ${styles.cornerLeft}`}>
          <CornerOpacity/>
        </b>
        <b className={`${styles.corner} ${styles.cornerBottom} ${styles.cornerRight}`}>
          <CornerOpacity/>
        </b>

        <div className={styles.logo}>
          <Logo/>
        </div>

        <div className={styles.borderLayerFooter}>
          <Share/>
          <div className={styles.gotoRG}>
            <a className={styles.gotoRGLink} rel="noopener noreferrer" href="//rg.ru" target="_blank">
              <News/>
            </a>
          </div>
        </div>
      </div>

      <div className={styles.header}>
        { getMenuItems() }
      </div>
    </div>
  )
}